<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'
  import Popper from 'popper.js'

  // Components
  import IconButton from '../components/IconButton.svelte'
  import Button from '../components/Button.svelte'

  // Stores
  import { urlData } from '../stores/router'

  // Helpers
  import { signOut } from '../helpers/auth'

  export let title

  let button
  let pop
  let control
  let hide = true

  function toggleMenu() {
    hide = !hide
    if (control) control.update()
  }

  function logout() {
    signOut()
    $urlData.segment = 'auth'
  }

  onMount(() => {
    control = new Popper(button, pop, { placement: 'bottom' })
  })
</script>

<style>
  div.app-bar {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    background-color: #fff;
  }

  div.logout div.top,
  div.logout div.menu {
    display: none;
  }

  div.top {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 100;
  }

  div.menu {
    z-index: 200;
  }

  h1 {
    text-align: center;
    opacity: 0.7;
  }

  .hide {
    display: none;
  }
</style>

<div
  transition:fade={{ duration: 300 }}
  class="app-bar"
  class:logout={$urlData.segment === 'auth'}>
  <h1 class="type-h2">{title}</h1>
  <div class="top" bind:this={button}>
    <IconButton icon="more_vert" on:click={toggleMenu} />
  </div>
  <div class="menu" bind:this={pop} class:hide>
    <Button on:click={logout}>Sair</Button>
  </div>
</div>
