<script>
  import { urlData } from '../stores/router'

  // Helpers
  import { slugifyConfigured } from '../helpers'

  // Components
  import Input from './Input.svelte'
  import AddModal from './AddModal.svelte'
  import Button from './Button.svelte'

  // Stores
  import { listTemplates, listFields } from '../stores/project'

  // API
  import { addTemplate } from '../api/template'

  let name = ''
  let slug = ''

  let focusName = false
  let close

  function clear() {
    name = ''
    slug = ''
  }

  let inputNode

  function callOpen({ detail }) {
    if (detail && inputNode) {
      inputNode.focus()
      inputNode.select()
    }
    if (!detail) clear()
  }

  function slugDigest(text) {
    let s = slugifyConfigured(text)

    if (text.trim() !== s) {
      return s
    }
    return text
  }

  $: if (focusName) slug = name

  function saveTemplate() {
    let toSend = {
      name,
      slug,
      order: $listTemplates.length,
    }
    if (close) close()
    setTimeout(() => {
      addTemplate(toSend).then(() => {
        clear()
      })
    }, 500)
  }
</script>

<style>
  div.template-form {
    width: 100%;
    display: grid;
    padding: 8px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 8px;
    justify-content: center;
    align-content: flex-start;
  }

  div.template-form .fullWidth {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
    flex-direction: column;
  }

  div.template-actions {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: stretch;
    padding: 16px 32px;
  }
</style>

<AddModal title="Nova Modelo" on:open={callOpen} bind:close>
  <div class="template-form">
    <div class="fullWidth">
      <Input
        label="Nome da Modelo"
        bind:node={inputNode}
        bind:value={name}
        on:input={() => (focusName = true)}
        on:blur={() => (focusName = false)} />
    </div>
    <div class="fullWidth">
      <Input bind:value={slug} digest={slugDigest} label="Slug da Modelo" />
    </div>
  </div>
  <div class="template-actions">
    <Button on:click={saveTemplate}>criar modelo</Button>
  </div>
</AddModal>
