<script>
  import { onMount } from 'svelte'

  // Motion
  import { flip } from 'svelte/animate'

  export let key = 'id'
  export let items = []

  let itemId = false
  let itemObj
  let index

  let height
  let listNode
  $: separator = height / items.length

  function getkey(item) {
    return typeof item === 'object' ? item[key] : item
  }

  $: if (itemId) {
    index = items.findIndex(i => getkey(i) === itemId)
  }

  function handleDragOver(evt) {
    evt.stopPropagation()
    evt.preventDefault()

    let y = evt.y || (evt.touches && evt.touches[0].clientY)
    if (Number.isNaN(y)) return true
    let bound = listNode.getBoundingClientRect()
    let pos = y - bound.y
    let calcIndex = ~~(pos / separator)

    if (calcIndex !== index && itemId) {
      let temp = items[index]
      items.splice(index, 1)
      index = calcIndex
      items.splice(index, 0, temp)
      items = [...items]
    }
    return false
  }
</script>

<style>
  ul {
    padding: 0;
    margin: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  li {
    overflow: hidden;
    list-style-type: none;
    min-height: 56px;
    width: 100%;
    margin-bottom: 8px;
    display: flex;
    flex-wrap: nowrap;
    z-index: -1;
    transition: background-color 0.3s;
  }

  li:hover {
    background-color: rgba(0, 0, 0, 0.12);
  }

  li > i {
    line-height: 56px;
    height: 56px;
    width: 48px;
    margin: 4;
    text-align: center;
    cursor: grab;
  }

  li.draging {
    cursor: grabbing;
  }

  div.content {
    flex: 1 0 auto;
    min-height: 56px;
  }
</style>

<ul
  on:mouseover={handleDragOver}
  on:touchmove={handleDragOver}
  on:mouseup={() => (itemId = false)}
  on:touchend={() => (itemId = false)}
  bind:clientHeight={height}
  bind:this={listNode}
  class:draging={!!itemId}>
  {#each items as item (getkey(item))}
    <li
      animate:flip={{ duration: 300 }}
      class:draging={itemId === getkey(item)}>
      <i
        class="material-icons"
        on:mousedown={() => (itemId = getkey(item))}
        on:touchstart={() => (itemId = getkey(item))}>
        drag_handle
      </i>
      <div class="content">
        <slot {item} />
      </div>
    </li>
  {/each}
</ul>
