<script>
  import { onMount, onDestroy, createEventDispatcher } from 'svelte'

  // Motion
  import { ripple } from 'fluido-components'
  import anime from 'animejs'

  export let title
  export let widthSize = 328

  const dispatch = createEventDispatcher()

  let open = false
  let started = false
  let toDispatch = false

  export function close() {
    started = true
    open = false
    toDispatch = true
  }

  let cardNode
  let svgNode

  const duration = {
    duration: 500,
    easing: 'easeInOutQuint',
  }

  const cardAnimation = {
    height: 72,
    width: 72,
    borderRadius: 40,
  }

  function handleOpen() {
    if (started) return
    started = true
    open = !open
    toDispatch = true
  }

  function handleExternalClick() {
    if (!open || started) return
    started = true
    open = false
    toDispatch = true
  }

  function preventClick(evt) {
    evt.stopPropagation()
  }

  onMount(() => {
    if (!open) {
      anime.set(cardNode, cardAnimation)
    } else {
      anime.set(svgNode, { rotate: 135 })
    }

    window.addEventListener('click', handleExternalClick)
  })

  onDestroy(() => {
    window.removeEventListener('click', handleExternalClick)
  })

  $: if (cardNode && svgNode) {
    if (open && started) {
      anime({
        targets: cardNode,
        ...duration,
        height: n => [...n.children].reduce((v, e) => v + e.offsetHeight, 0),
        width: () => widthSize,
        borderRadius: 0,
        complete: () => {
          started = false
          if (toDispatch) dispatch('open', open)
          toDispatch = false
        },
      })
      anime({
        targets: svgNode,
        ...duration,
        rotate: 135,
      })
    } else if (started) {
      anime({
        targets: cardNode,
        ...duration,
        ...cardAnimation,
        complete: () => {
          started = false
          if (toDispatch) dispatch('open', open)
          toDispatch = false
        },
      })
      anime({
        targets: svgNode,
        ...duration,
        rotate: 0,
      })
    }
  }
</script>

<style>
  div.main {
    box-sizing: border-box;
    position: relative;
    height: 72px;
    width: 72px;
    overflow: visible;
    z-index: 2000;
  }

  .svg,
  svg {
    position: absolute;
    top: calc(50% - 32px);
    left: calc(50% - 32px);
    width: 64px;
    height: 64px;
    border-radius: 100%;
  }

  div.card {
    position: absolute;
    box-sizing: border-box;
    border: 2px solid var(--color-black);
    background-color: #ffffff;
    top: 0;
    left: 0;
    overflow: hidden;
  }

  h3 {
    height: 72px;
    line-height: 72px;
    min-width: 328px;
    text-align: center;
  }

  div.form {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-bottom: 16px;
  }

  div.form > * {
    min-width: 328px;
  }

  div.form:empty,
  div.actions:empty {
    display: none;
  }

  .text {
    margin-bottom: 8px;
    text-align: center;
    width: 72px;
    height: 20px;
    line-height: 20px;
  }
</style>

<div class="type-button text">criar</div>
<div class="main" class:open on:click={preventClick}>
  <div class="card" bind:this={cardNode}>
    <h3 class="type-overline">{title || 'Card Title'}</h3>
    <div class="form">
      <slot />
    </div>
    <div class="actions">
      <slot name="actions" />
    </div>
  </div>
  <div use:ripple on:click={handleOpen} class="svg">
    <svg
      bind:this={svgNode}
      xmlns="http://www.w3.org/2000/svg"
      width="64"
      height="64"
      viewBox="0 0 24 24">
      <path d="M19 13h-6v6h-2v-6H5v-2h6V5h2v6h6v2z" />
      <path d="M0 0h24v24H0z" fill="none" />
    </svg>
  </div>
</div>
