<script>
  import { fade } from 'svelte/transition'
  import TinyColor from '@ctrl/tinycolor'

  export let label = ''
  export let color = '#ffffff'
  export let digest = null
  export let emphasis = false

  let primaryColor = '#ffffff'
  let secondaryColor = 'var(--color-black)'

  $: {
    let ptc = new TinyColor(color)
    if (ptc.isValid) {
      ptc.setAlpha(1)
      primaryColor = ptc.toHexString()
    } else {
      primaryColor = '#ffffff'
    }
    let stc = new TinyColor(primaryColor)
    if (stc.isDark()) {
      secondaryColor = '#ffffff'
    } else {
      secondaryColor = 'var(--color-black)'
    }
  }

  export let node = null
  export let value = ''

  $: if (typeof digest === 'function') value = digest(value)
</script>

<style>
  input {
    height: 48px;
    padding: 0 16px;
    border: 2px solid rgba(0, 0, 0, 0.3);
    border-radius: 26px;
    outline: none;
    transition: border 0.5s, border-radius 0.5s;
    background-color: var(--input-primary-color, #ffffff);
    color: var(--input-secondary-color, var(--color-black));
  }

  input.emphasis {
    height: 56px;
    border-radius: 30px;
  }

  input:focus {
    border-radius: 0;
    border: 2px solid rgba(0, 0, 0, 0.7);
  }
</style>

<input
  on:input
  on:blur
  on:change
  on:focus
  on:keypress
  trasition:fade
  style="--input-primary-color: {primaryColor}; --input-secondary-color: {secondaryColor}"
  bind:this={node}
  bind:value
  placeholder={label}
  class="type-subtitle2"
  class:emphasis />
