<script>
  import TinyColor from '@ctrl/tinycolor'
  import { onMount } from 'svelte'
  import { fly } from 'svelte/transition'

  import { currentProject } from '../stores/project'
  import { snackList } from '../stores/main'

  $: primaryColor = $currentProject
    ? $currentProject.primaryColor || '#ffffff'
    : '#ffffff'
  $: secondaryColor = $currentProject
    ? $currentProject.secondaryColor || '#121212'
    : '#121212'
  $: light = new TinyColor(primaryColor).isLight()

  $: list = $snackList.slice(-4)
</script>

<style>
  div.snack-list {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 360px;
    min-height: 56px;
    padding: 4px;
    z-index: 3000;
  }

  div.snack-list:empty {
    display: none;
  }

  div.snack-list > div.snack {
    margin: 4px;
    width: 360px;
    min-height: 56px;
    background-color: var(--snack-primary-color, #ffffff);
    color: var(--snack-secondary-color, var(--color-black));
    box-sizing: border-box;
  }

  div.snack-list.light > div.snack {
    border: 2px solid currentColor;
  }
</style>

<div
  class="snack-list"
  style="--snack-primary-color: {primaryColor}; --snack-secondary-color: {secondaryColor};"
  class:light>
  {#each list as snack (snack.__id)}
    <div transition:fly={{ x: -330, duration: 300 }} class="snack">
      {snack.text}
    </div>
  {/each}
</div>
