<script>
  import { urlData } from '../stores/router'

  export let name = 'Exemplo de Projeto'
  export let slug = ''
  export let primaryColor = '#ffffff'
  export let secondaryColor = '#121212'
  export let link = '#'
  export let properties = {}
  export let lastUpdate = null
  export let created = null

  $: initials = name
    .split(/ +/)
    .map(e => e[0].toUpperCase())
    .join('')

  function handleClick() {
    if (slug) {
      urlData.set({
        segment: 'templates',
        query: { project: slug },
      })
    }
  }
</script>

<style>
  div {
    background-color: var(--card-primary-color, #ffffff);
    height: 72px;
    padding: 8px;
    box-sizing: border-box;
    display: grid;
    grid-template-areas:
      'initials icon'
      'name name';
    grid-template-columns: 1fr 24px;
    cursor: pointer;
  }

  h4 {
    grid-area: name;
    color: var(--card-secondary-color, var(--color-black));
  }

  span {
    grid-area: initial;
    color: var(--card-secondary-color, var(--color-black));
  }

  a {
    grid-area: icon;
    color: var(--card-secondary-color, #ffffff);
    border-radius: 100%;
    height: 24px;
    width: 24px;
  }

  a > * {
    color: inherit;
  }
</style>

<div
  on:click={handleClick}
  style="--card-primary-color: {primaryColor}; --card-secondary-color: {secondaryColor}">
  <h4 class="type-overline">{name}</h4>
  <span class="type-h5">{initials}</span>
  <a href={link}>
    <i class="material-icons">open_in_new</i>
  </a>
</div>
