<script>
  export let smallQuantity = 1
  export let mediumQuantity = 2
  export let largeQuantity = 3

  export let smallLimit = 720
  export let mediumLimit = 1080

  export let index = 0

  let wrapper
  $: quantity = wrapper ? wrapper.children.length || 1 : 1
  $: cIndex = index < 0 ? 0 : index > quantity ? quantity : index
</script>

<style>
  div.swipe {
    position: relative;
    overflow: hidden;
    width: 100%;
  }

  div.wrapper {
    width: calc(100% * var(--quantity, 1));
    display: grid;
    grid-template-columns: repeat(var(--quantity, 1), 1fr);
    grid-template-rows: 1fr;
    transform: translateX(
      calc(-100% * calc(var(--index, 0) / var(--quantity, 1)))
    );
    transition: transform 0.3s;
  }
</style>

<div class="swipe" style="--quantity: {quantity}; --index: {cIndex}">
  <div class="wrapper" bind:this={wrapper}>
    <slot />
  </div>
</div>
