<script context="module">
  export let bar = 'Autenticação'
  export let hideSearch = true
</script>

<script>
  import { onMount } from 'svelte'

  // Stores
  import { snackList } from '../stores/main'
  import { urlData } from '../stores/router'

  import { getUserAsync, startFirebaseUI } from '../helpers/auth'

  let message = ''

  onMount(() => {
    getUserAsync()
      .then(user => {
        message = `Já logado com usuário <strong>${user.displayName}</strong>`
        setTimeout(() => ($urlData.segment = 'projects'), 2000)
      })
      .catch(() => {
        startFirebaseUI('firebase-auth')
      })
  })
</script>

<style>
  section {
    overflow: hidden;
    padding: 16px;
    text-align: center;
  }
</style>

<section>
  <div id="firebase-auth" />
  {#if !!message}
    <h2 class="type-subtitle1">
      {@html message}
    </h2>
    <span class="type-caption">Redirecionando...</span>
  {/if}
</section>
