<script>
  // Components
  import Input from './Input.svelte'
  import AddModal from './AddModal.svelte'
  import Button from './Button.svelte'

  // Stores
  import { urlData } from '../stores/router'

  // API
  import { addProject } from '../api/project'

  let primaryColor = '#FFFFFF'
  let secondaryColor = '#000000'
  let name = ''

  let focusName = false
  let close

  function clear() {
    primaryColor = '#FFFFFF'
    secondaryColor = '#000000'
    name = ''
  }

  let inputNode

  function callOpen({ detail }) {
    if (detail && inputNode) {
      inputNode.focus()
      inputNode.select()
    }
    if (!detail) clear()
  }

  function saveProject() {
    let toSend = {
      name,
      primaryColor,
      secondaryColor,
    }
    if (close) close()
    setTimeout(() => {
      addProject(toSend)
        .then(project => {
          clear()
          urlData.set({
            segment: 'templates',
            query: { project: project.slug },
          })
        })
        .catch(err => console.error('ERROR', err))
    }, 500)
  }
</script>

<style>
  div.project-form {
    display: grid;
    padding: 8px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 8px;
    justify-content: center;
    align-content: flex-start;
  }

  div.project-form .fullWidth {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
    flex-direction: column;
  }

  div.project-actions {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: stretch;
    padding: 16px 32px;
  }
</style>

<AddModal title="Novo Projeto" on:open={callOpen} bind:close>
  <div class="project-form">
    <div class="fullWidth">
      <Input
        label="Nome do Projeto"
        bind:node={inputNode}
        bind:value={name}
        on:input={() => (focusName = true)}
        on:blur={() => (focusName = false)} />
    </div>
    <Input bind:value={primaryColor} color={primaryColor} />
    <Input bind:value={secondaryColor} color={secondaryColor} />
  </div>
  <div class="project-actions">
    <Button on:click={saveProject}>criar projeto</Button>
  </div>
</AddModal>
